import { Block, intlUtils, Step } from '@navikt/fp-common';
import actionCreator from 'app/context/action/actionCreator';
import SknadRoutes from 'app/routes/routes';
import { Hovedknapp } from 'nav-frontend-knapper';
import { Element, Normaltekst } from 'nav-frontend-typografi';
import React, { useState } from 'react';
import { useIntl } from 'react-intl';
import useOnValidSubmit from 'app/utils/hooks/useOnValidSubmit';
import useSknad from 'app/utils/hooks/useSknad';
import useSkerinfo from 'app/utils/hooks/useSkerinfo';
import useAvbrytSknad from 'app/utils/hooks/useAvbrytSknad';
import stepConfig, { getPreviousStepHref } from '../stepsConfig';
import AndreInntekter from './components/andre-inntekter/AndreInntekter';
import ArbeidsforholdInformasjon from './components/arbeidsforhold-informasjon/ArbeidsforholdInformasjon';
import EgenNring from './components/egen-nring/EgenNring';
import Frilans from './components/frilans/Frilans';
import InfoTilFiskere from './components/info-til-fiskere/InfoTilFiskere';
import { InntektsinformasjonFormComponents, InntektsinformasjonFormData } from './inntektsinformasjonFormConfig';
import {
    getInitialInntektsinformasjonFormValues,
    mapInntektsinformasjonFormDataToState,
} from './inntektsinformasjonFormUtils';
import inntektsinforMasjonQuestionsConfig from './inntektsInformasjonQuestionsConfig';
import { storeAppState } from 'app/utils/submitUtils';
import { ForeldrepengesknadContextState } from 'app/context/ForeldrepengesknadContextConfig';
import useFortsettSknadSenere from 'app/utils/hooks/useFortsettSknadSenere';
import { getAktiveArbeidsforhold } from 'app/utils/arbeidsforholdUtils';
import { ISOStringToDate } from 'app/utils/dateUtils';
import { getFamiliehendelsedato } from 'app/utils/barnUtils';
import useSaveLoadedRoute from 'app/utils/hooks/useSaveLoadedRoute';

const Inntektsinformasjon = () => {
    const intl = useIntl();
    const { arbeidsforhold } = useSkerinfo();
    const { sker, barn } = useSknad();
    const familiehendelsesdato = getFamiliehendelsedato(barn);

    const [frilansoppdrag, setFrilansoppdrag] = useState(
        sker.frilansInformasjon ? sker.frilansInformasjon.oppdragForNreVennerEllerFamilieSiste10Mnd : []
    );
    const [egenNringInformasjon, setEgenNringsInformasjon] = useState(
        sker.selvstendigNringsdrivendeInformasjon ? sker.selvstendigNringsdrivendeInformasjon : []
    );
    const [andreInntekterInformasjon, setAndreInntekterInformasjon] = useState(
        sker.andreInntekterSiste10Mnd ? sker.andreInntekterSiste10Mnd : []
    );

    const onValidSubmitHandler = (values: Partial<InntektsinformasjonFormData>) => {
        const updatedSker = mapInntektsinformasjonFormDataToState(
            values,
            sker,
            andreInntekterInformasjon,
            frilansoppdrag,
            egenNringInformasjon
        );

        return [actionCreator.setSker(updatedSker)];
    };

    const { handleSubmit, isSubmitting } = useOnValidSubmit(
        onValidSubmitHandler,
        SknadRoutes.OPPSUMMERING,
        (state: ForeldrepengesknadContextState) => storeAppState(state)
    );
    const onAvbrytSknad = useAvbrytSknad();
    const onFortsettSknadSenere = useFortsettSknadSenere();
    useSaveLoadedRoute(SknadRoutes.INNTEKTSINFORMASJON);

    return (
        <InntektsinformasjonFormComponents.FormikWrapper
            initialValues={getInitialInntektsinformasjonFormValues(sker)}
            onSubmit={handleSubmit}
            renderForm={({ values: formValues }) => {
                const visibility = inntektsinforMasjonQuestionsConfig.getVisbility(formValues);

                return (
                    <Step
                        bannerTitle={intlUtils(intl, 'sknad.pageheading')}
                        backLinkHref={getPreviousStepHref('inntektsinformasjon')}
                        activeStepId="inntektsinformasjon"
                        pageTitle={intlUtils(intl, 'sknad.inntektsinformasjon')}
                        stepTitle={intlUtils(intl, 'sknad.inntektsinformasjon')}
                        onCancel={onAvbrytSknad}
                        onContinueLater={onFortsettSknadSenere}
                        steps={stepConfig(intl)}
                        kompakt={true}
                    >
                        <InntektsinformasjonFormComponents.Form includeButtons={false} includeValidationSummary={true}>
                            <Block padBottom="l">
                                <Element>Utbetalinger fra NAV</Element>
                                <Normaltekst>
                                    Hvis du fr utbetalinger fra NAV, trenger du ikke  opplyse om det i sknaden
                                </Normaltekst>
                            </Block>

                            <ArbeidsforholdInformasjon
                                arbeidsforhold={getAktiveArbeidsforhold(
                                    arbeidsforhold,
                                    ISOStringToDate(familiehendelsesdato)
                                )}
                            />

                            <InfoTilFiskere />

                            <Block padBottom="l">
                                <Frilans
                                    frilansoppdrag={frilansoppdrag}
                                    setFrilansoppdrag={setFrilansoppdrag}
                                    visibility={visibility}
                                    formValues={formValues}
                                />
                            </Block>

                            <Block padBottom="l">
                                <EgenNring
                                    egenNringInformasjon={egenNringInformasjon}
                                    setEgenNringsInformasjon={setEgenNringsInformasjon}
                                    visibility={visibility}
                                    formValues={formValues}
                                />
                            </Block>

                            <Block padBottom="l">
                                <AndreInntekter
                                    andreInntekterInformasjon={andreInntekterInformasjon}
                                    setAndreInntekterInformasjon={setAndreInntekterInformasjon}
                                    visibility={visibility}
                                    formValues={formValues}
                                />
                            </Block>

                            <Block textAlignCenter={true} visible={visibility.areAllQuestionsAnswered()}>
                                <Hovedknapp disabled={isSubmitting} spinner={isSubmitting}>
                                    {intlUtils(intl, 'sknad.gVidere')}
                                </Hovedknapp>
                            </Block>
                        </InntektsinformasjonFormComponents.Form>
                    </Step>
                );
            }}
        />
    );
};

export default Inntektsinformasjon;
