import { dateToISOString, YesOrNo } from '@navikt/sif-common-formik/lib';
import { AnnenInntekt, AnnenInntektType } from 'app/context/types/AnnenInntekt';
import { Frilans, FrilansOppdrag } from 'app/context/types/Frilans';
import { Nring } from 'app/context/types/Nring';
import Sker from 'app/context/types/Sker';
import { ISOStringToDate } from 'app/utils/dateUtils';
import { convertBooleanOrUndefinedToYesOrNo, convertYesOrNoOrUndefinedToBoolean } from 'app/utils/formUtils';
import { replaceInvisibleCharsWithSpace } from 'app/utils/stringUtils';
import { InntektsinformasjonFormData, InntektsinformasjonFormField } from './inntektsinformasjonFormConfig';

export const initialInntektsinformasjonFormValues: InntektsinformasjonFormData = {
    [InntektsinformasjonFormField.hattInntektSomFrilans]: YesOrNo.UNANSWERED,
    [InntektsinformasjonFormField.hattInntektSomNringsdrivende]: YesOrNo.UNANSWERED,
    [InntektsinformasjonFormField.hattAndreInntekter]: YesOrNo.UNANSWERED,
    [InntektsinformasjonFormField.frilansOppstartsDato]: '',
    [InntektsinformasjonFormField.oppdragForNreVennerEllerFamilie]: YesOrNo.UNANSWERED,
    [InntektsinformasjonFormField.inntektSomFosterforelder]: YesOrNo.UNANSWERED,
    [InntektsinformasjonFormField.jobberFremdelesSomFrilanser]: YesOrNo.UNANSWERED,
    [InntektsinformasjonFormField.hattAndreInntekter]: YesOrNo.UNANSWERED,
};

const cleanUpRegnskapsfrerNring = (nring: Nring): Nring => {
    if (nring.harRegnskapsfrer) {
        return {
            ...nring,
            regnskapsfrer: {
                ...nring.regnskapsfrer!,
                navn: replaceInvisibleCharsWithSpace(nring.regnskapsfrer!.navn),
            },
        };
    }
    return nring;
};

export const cleanupInvisibleCharsFromNring = (nring: Nring): Nring => {
    const cleanedNavn = replaceInvisibleCharsWithSpace(nring.navnPNringen);
    if (nring.hattVarigEndringAvNringsinntektSiste4Kalenderr) {
        const cleanedEndringInformasjon = {
            ...nring.endringAvNringsinntektInformasjon!,
            forklaring: replaceInvisibleCharsWithSpace(nring.endringAvNringsinntektInformasjon!.forklaring),
        };

        return {
            ...cleanUpRegnskapsfrerNring(nring),
            navnPNringen: cleanedNavn,
            endringAvNringsinntektInformasjon: cleanedEndringInformasjon,
        };
    }
    return {
        ...cleanUpRegnskapsfrerNring(nring),
        navnPNringen: cleanedNavn,
    };
};

export const cleanupInvisibleCharsFromFrilansinformasjon = (frilansoppdrag: FrilansOppdrag[]): FrilansOppdrag[] => {
    return frilansoppdrag.map((oppdrag: FrilansOppdrag) => ({
        ...oppdrag,
        navnPArbeidsgiver: replaceInvisibleCharsWithSpace(oppdrag.navnPArbeidsgiver),
    }));
};

export const cleanupInvisibleCharsFromAndreInntekter = (andreInntekter: AnnenInntekt[]): AnnenInntekt[] => {
    return andreInntekter.map((inntekt) =>
        inntekt.type === AnnenInntektType.JOBB_I_UTLANDET
            ? {
                  ...inntekt,
                  arbeidsgiverNavn: replaceInvisibleCharsWithSpace(inntekt.arbeidsgiverNavn),
              }
            : inntekt
    );
};

export const mapInntektsinformasjonFormDataToState = (
    values: Partial<InntektsinformasjonFormData>,
    sker: Sker,
    andreInntekter?: AnnenInntekt[],
    frilansoppdrag?: FrilansOppdrag[],
    nringer?: Nring[]
): Sker => {
    let frilansInformasjon: Frilans | undefined = undefined;

    if (values.hattInntektSomFrilans === YesOrNo.YES) {
        frilansInformasjon = {
            oppstart: ISOStringToDate(values.frilansOppstartsDato)!,
            jobberFremdelesSomFrilans: convertYesOrNoOrUndefinedToBoolean(values.jobberFremdelesSomFrilanser)!,
            harJobbetForNrVennEllerFamilieSiste10Mnd: convertYesOrNoOrUndefinedToBoolean(
                values.oppdragForNreVennerEllerFamilie
            )!,
            driverFosterhjem: convertYesOrNoOrUndefinedToBoolean(values.inntektSomFosterforelder),
            oppdragForNreVennerEllerFamilieSiste10Mnd: cleanupInvisibleCharsFromFrilansinformasjon(frilansoppdrag!),
        };
    }

    return {
        erAleneOmOmsorg: sker.erAleneOmOmsorg,
        sprkkode: sker.sprkkode,
        harHattAnnenInntektSiste10Mnd: convertYesOrNoOrUndefinedToBoolean(values.hattAndreInntekter)!,
        harJobbetSomFrilansSiste10Mnd: convertYesOrNoOrUndefinedToBoolean(values.hattInntektSomFrilans)!,
        harJobbetSomSelvstendigNringsdrivendeSiste10Mnd: convertYesOrNoOrUndefinedToBoolean(
            values.hattInntektSomNringsdrivende
        )!,
        andreInntekterSiste10Mnd:
            values.hattAndreInntekter === YesOrNo.YES ? cleanupInvisibleCharsFromAndreInntekter(andreInntekter!) : [],
        selvstendigNringsdrivendeInformasjon:
            values.hattInntektSomNringsdrivende === YesOrNo.YES
                ? nringer!.map((nring) => cleanupInvisibleCharsFromNring(nring))
                : [],
        frilansInformasjon: values.hattInntektSomFrilans === YesOrNo.YES ? frilansInformasjon : undefined,
    };
};

export const getInitialInntektsinformasjonFormValues = (sker: Sker): InntektsinformasjonFormData => {
    return {
        ...initialInntektsinformasjonFormValues,
        hattAndreInntekter: convertBooleanOrUndefinedToYesOrNo(sker.harHattAnnenInntektSiste10Mnd),
        hattInntektSomNringsdrivende: convertBooleanOrUndefinedToYesOrNo(
            sker.harJobbetSomSelvstendigNringsdrivendeSiste10Mnd
        ),
        hattInntektSomFrilans: convertBooleanOrUndefinedToYesOrNo(sker.harJobbetSomFrilansSiste10Mnd),
        frilansOppstartsDato: sker.frilansInformasjon ? dateToISOString(sker.frilansInformasjon.oppstart) : '',
        inntektSomFosterforelder: sker.frilansInformasjon
            ? convertBooleanOrUndefinedToYesOrNo(sker.frilansInformasjon.driverFosterhjem)
            : YesOrNo.UNANSWERED,
        jobberFremdelesSomFrilanser: sker.frilansInformasjon
            ? convertBooleanOrUndefinedToYesOrNo(sker.frilansInformasjon.jobberFremdelesSomFrilans)
            : YesOrNo.UNANSWERED,
        oppdragForNreVennerEllerFamilie: sker.frilansInformasjon
            ? convertBooleanOrUndefinedToYesOrNo(sker.frilansInformasjon.harJobbetForNrVennEllerFamilieSiste10Mnd)
            : YesOrNo.UNANSWERED,
    };
};
